.header{
   height:80px;
   background-color: #fff;
   .header-top{
      height:40px;
      line-height: 40px;
      padding-right:30px;
      text-align: right;
      border-bottom: 1px solid #4936f1;
      span{
         margin-right: 15px;
      }
   }

   .header-bottom{
      height:40px;
      display:flex;
      align-items:center;

      .header-bottom-left{
         position: relative;
         width:25%;
         text-align: center;
         font: size 20px;
         &::after{
            content: '';
            position: absolute;
            right: 50%;
            top:100%;
            transform: translateX(50%);
            border-top: 20px solid rgb(28, 229, 243) ;
            border-right: 20px solid transparent ;
            border-bottom: 20px solid transparent;
            border-left: 20px solid transparent ;
          }

      }


      .header-bottom-right{
         width:75%;
         text-align: right;
         margin-right: 30px;
         img{
            margin:0px 15px;
            width:30px;
            height:20px
         }
      }
   }
}