.login{
    width:100%;
    height:100%;
    background-image: url('./images/bg.jpg');
    background-size: 100%  100%;

    
    .login-header {
      display: flex;
      align-items: center;
      height: 80px;
      background-color: rgba(21,20,13,0.5);
      img{
          width: 40px;
          height: 40px;  
          margin: 0 15px 0 50px;
      }
      h1{
          font-size: 30px;
          color: white;
      }

    }

    
    .login-content{
        width: 500px;
        height: 300px;
        background-color: rgb(211, 240, 252);
        margin: 50px  auto;
        padding: 20px 60px;
        
        h2{
            text-align: center;
            font-size: 30px;
            font-weight: bold;
            margin-bottom:20px;
        }
        
        .login-form-buttom{
            width:100%,
        }
    }
}
